import clsx from "clsx";
import s from "./ButtonBack.module.css";

export const ButtonBack = ({ className, onClick }) => {
  return (
    <button className={clsx(s.button, className)} onClick={onClick}>
      <svg
        xmlns="http://www.w3.org/2000/svg"
        width="24"
        height="24"
        viewBox="0 0 24 24"
        fill="none"
      >
        <path
          d="M20 11H7.83L13.42 5.41L12 4L4 12L12 20L13.41 18.59L7.83 13H20V11Z"
          fill="black"
        />
      </svg>
      <p className={s.btnText}>GO BACK</p>
    </button>
  );
};
